'use strict'

const ajaxSend = async (formData) => {
  // —Å–æ–∑–¥–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º—ã
  const fetchResp = await fetch('telegram.php', {
    // —É–∫–∞–∑—ã–≤–∞–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Ñ–æ—Ä–º—ã ‚Äî telegram.php
    method: 'POST', // –º–µ—Ç–æ–¥, –∫–æ—Ç–æ—Ä—ã–º –º—ã –æ—Ç–ø—Ä–∞–≤–ª—è–µ–º —Ñ–æ—Ä–º—É
    body: formData, // —á—Ç–æ –±—É–¥–µ—Ç –≤–Ω—É—Ç—Ä–∏ —Ñ–æ—Ä–º—ã ‚Äî —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ input
  })
  if (!fetchResp.ok) {
    // –µ—Å–ª–∏ –æ—à–∏–±–∫–∞, —Ç–æ...
    throw new Error(
      `–û—à–∏–±–∫–∞ –ø–æ –∞–¥—Ä–µ—Å—É ${url}, —Å—Ç–∞—Ç—É—Å –æ—à–∏–±–∫–∏ ${fetchResp.status}`
    ) // –≤—ã–≤–æ–¥–∏–º —Å—Ç–∞—Ç—É—Å –æ—à–∏–±–∫–∏ –∏ —Ç–µ–∫—Å—Ç
  }
  return await fetchResp.text() // –µ—Å–ª–∏ –≤—Å–µ —Ö–æ—Ä–æ—à–æ, –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –æ—Ç–≤–µ—Ç —Å–µ—Ä–≤–µ—Ä–∞
}

const forms = document.querySelectorAll('form') // –Ω–∞—Ö–æ–¥–∏–º –≤—Å–µ —Ç–µ–≥–∏ form
forms.forEach((form) => {
  // –¥–ª—è –∫–∞–∂–¥–æ–π —Ñ–æ—Ä–º—ã...
  form.addEventListener('submit', function (e) {
    // –æ—Ç—Å–ª–µ–∂–∏–≤–∞–µ–º —Å–æ–±—ã—Ç–∏–µ –æ—Ç–ø—Ä–∞–≤–∫–∏
    e.preventDefault() // –æ—Ç–º–µ–Ω–∏—Ç—å —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—É—é –æ—Ç–ø—Ä–∞–≤–∫—É —Ñ–æ—Ä–º—ã
    const formData = new FormData(this) // —Å–æ–±–∏—Ä–∞–µ–º –≤—Å–µ –¥–∞–Ω–Ω—ã–µ –∏–∑ —Ñ–æ—Ä–º—ã
    console.log(formData)

    ajaxSend(formData) // –ø–µ—Ä–µ–¥–∞–µ–º –¥–∞–Ω–Ω—ã–µ –∏–∑ —Ñ–æ—Ä–º—ã –≤ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫
      .then((response) => {
        // –µ—Å–ª–∏ –≤—Å–µ —É—Å–ø–µ—à–Ω–æ, —Ç–æ..
        this.innerHTML =
          '–°–ø–∞—Å–∏–±–æ,<br> –í–∞—à–µ —Å–æ–æ–±—â–µ–Ω–∏–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ –≤ —Ç–µ–ª–µ–≥—Ä–∞–º. –í –±–ª–∏–∂–∞–π—à–µ–µ –≤—Ä–µ–º—è –æ—Ç–≤–µ—á—É –Ω–∞ –ø–æ—á—Ç—É, —É–∫–∞–∑–∞–Ω–Ω—É—é –í–∞–º–∏ –≤ —Ñ–æ—Ä–º–µ.' /* –æ–∫–Ω–æ –±–ª–∞–≥–æ–¥–∞—Ä–Ω–æ—Å—Ç–∏ */

        form.reset() /*  –æ—á–∏—â–∞–µ–º –ø–æ–ª—è —Ñ–æ—Ä–º—ã */
      })
      .catch((err) => console.error(err)) /* –µ—Å–ª–∏ –æ—à–∏–±–∫–∞, –≤—ã–≤–æ–¥–∏–º –≤ –∫–æ–Ω—Å–æ–ª—å */
  })
})
